* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html,
body,
pre,
code,
kbd,
samp {
  font-family: "Press Start 2P", cursive;
}
.Form {
  .nes-container {
    margin: 2rem;
    width: 40rem;
  }
}
